function getFileTypeFromBase64string(input) {
  return input.replace('data:', '').split(';base64')[0];
}

function getBase64FromFile(file) {
  return new Promise((resolve, reject) => {
    const reader = new window.FileReader();

    reader.onload = () => {
      resolve(reader.result);
    };

    reader.onerror = () => reject;

    reader.readAsDataURL(file);
  });
}

function getImageFromPasteEvent(event) {
  return new Promise((resolve, reject) => {
    if (!event.clipboardData.items) {
      const waitInterval = setInterval(() => {
        if (this.event.target.children.length > 0) {
          clearInterval(waitInterval);

          resolve({
            base64string: this.event.target.children[0].src,
            type: getFileTypeFromBase64string(this.event.target.children[0].src)
          });

          this.event.target.innerHTML = '';
        }
      }, 1);

    } else {
      Array.from(event.clipboardData.items).some((item) => {
        if (item.kind === 'file' && (item.type === 'image/png' || item.type === 'image/jpeg')) {
          const file = item.getAsFile();

          const imageType = item.type;

          getBase64FromFile(file).then((result) => {
            resolve({
              base64string: result,
              type: imageType
            });
          });

          return true;
        }
      });
    }
  });
}

function getImageFromInputEvent(event) {
  return new Promise((resolve, reject) => {
    if (event.target.files && event.target.files[0]) {
      getBase64FromFile(event.target.files[0]).then((result) => {
        resolve({
          base64string: result,
          type: event.target.files[0].type
        });
      });
    } else {
      reject();
    }
  });
}

/**
 * Enables draggable functionality to an element
 * @param  {Element} element
 * @param  {Object} settings Callbacks
 */
function draggableElement(element, settings) {
  let elementDraggable = false;
  const startPosition = {
    left: 0,
    top: 0
  };
  const elementMargin = {
    left: 0,
    top: 0
  };

  const elementInitialMargin = {
    left: 0,
    top: 0
  };

  const mouseUpHandler = function() {
    elementDraggable = false;
    document.removeEventListener('mouseup', mouseUpHandler);
    document.removeEventListener('mousemove', mouseMoveHandler);

    if('onDragEnd' in settings) {
      settings.onDragEnd(elementMargin);
    };
  };

  const mouseMoveHandler = function(event) {
    if (elementDraggable) {

      if (settings.y) {
        elementMargin.top = (event.pageY - startPosition.top) + elementInitialMargin.top;
      }

      if (settings.x) {
        elementMargin.left = (event.pageX - startPosition.left) + elementInitialMargin.left;
      }


      if (settings.y) {
        element.style.marginTop = elementMargin.top + 'px';
      }

      if (settings.x) {
        element.style.marginLeft = elementMargin.left + 'px';
      }

    }
  };

  const mouseDownHandler = function(event) {
    event.preventDefault();
    elementDraggable = true;


    if (settings.y) {
      startPosition.top = event.pageY;
    }

    if (settings.x) {
      startPosition.left = event.pageX;
    }

    if (settings.y) {
      elementInitialMargin.top = parseInt(element.style.marginTop || 0);
    }

    if (settings.x) {
      elementInitialMargin.left = parseInt(element.style.marginLeft || 0);
    }

    document.addEventListener('mouseup', mouseUpHandler);
    document.addEventListener('mousemove', mouseMoveHandler);
  }

  element.addEventListener('mousedown', mouseDownHandler);
}

/**
 * Simple is object check.
 * @param item
 * @returns {boolean}
 */
function isObject(item) {
  return (item && typeof item === 'object' && !Array.isArray(item));
}

/**
 * Deep merge two objects.
 * @param target
 * @param source
 */
function mergeDeep(target, source) {
  if (isObject(target) && isObject(source)) {
    for (const key in source) {
      if (isObject(source[key])) {
        if (!target[key]) Object.assign(target, { [key]: {} });
        mergeDeep(target[key], source[key]);
      } else {
        Object.assign(target, { [key]: source[key] });
      }
    }
  }
  return target;
}

export default {
  getImageFromPasteEvent,
  getImageFromInputEvent,
  draggableElement,
  mergeDeep
};



