export default `

	.ico { display: inline-block; vertical-align: middle; background-repeat: no-repeat; background-size: contain; background-position: center center; }

	.ico-eye-slash { background-image: url(data:image/png;base64,); width: 17px; height: 13px; }
	a.active .ico-eye-slash { background-image: url(data:image/png;base64,); }

	.ico-eye-slash-small { background-image: url(data:image/png;base64,); width: 15px; height: 11px; }
	a.active .ico-eye-slash-small,
	a:hover .ico-eye-slash-small { background-image: url(data:image/png;base64,); }

	.ico-minimize { background-image: url(data:image/png;base64,); width: 8px; height: 2px; }
	a.active .ico-minimize,
	a:hover .ico-minimize {  background-image: url(data:image/png;base64,); }

	.ico-maximize { background-image: url(data:image/png;base64,); width: 10px; height: 10px; }
	a.active .ico-maximize,
	a:hover .ico-maximize { background-image: url(data:image/png;base64,); }

	.ico-close { background-image: url(data:image/png;base64,); width: 11px; height: 10px; }
	a.active .ico-close,
	a:hover .ico-close { background-image: url(data:image/png;base64,); }

	.ico-lock { background-image: url(data:image/png;base64,); width: 13px; height: 14px; }
	a.active .ico-lock { background-image: url(data:image/png;base64,); }

	.ico-color { background-image: url(data:image/png;base64,); width: 16px; height: 15px; }
	a.active .ico-color { background-image: url(data:image/png;base64,); }
`;



