<template>
    <div id="pixelParallel">
        <panel :config="config" :on-image-change="handleImageChange" />

        <overlay :config="config" :image="image" />
    </div>

</template>

<script>
import Mousetrap from 'mousetrap';
import DB from './utils/db.js';
import utils from './utils/utils.js';
import Panel from './components/Panel.vue';
import Overlay from './components/Overlay.vue';

const db = new DB();

export default {
    name: 'pixelParallel',
    data () {
        return {
            config: {
                currentPane: 'image',
                visible: true,
                minimized: true,
                horizontalGrid: {
                    enabled: false,
                    width: 960,
                    columns: 12,
                    gutter: 30,
                    opacity: .2
                },
                verticalGrid: {
                    enabled: false,
                    gutter: 30,
                    opacity: .1

                },
                rulers: {
                    enabled: false,
                    x: 10,
                    y: 10
                },
                image: {
                    top: 0,
                    left: 0,
                    opacity: .5,
                    scale: 1,
                    difference: false,
                    lock: false,
                    enabled: false
                }
            },
            image: {
                base64string: null,
                type: null
            }
        }
    },

    beforeCreate() {
        db.getProps().then((config) => {
            this.config = utils.mergeDeep(this.config, config);
        }).catch(() => {
            // No entry in the db
            this.config.minimized = false;
        });

        db.getImage().then((imageObject) => {
            this.image = imageObject;
        }).catch(() => {});

    },

    mounted() {
        Mousetrap
            .bind('f9', () => {
                this.config.minimized = !this.config.minimized;
            })
            .bind('f10', () => {
                this.config.visible = !this.config.visible;
            })
            .bind('ctrl+alt+left', () => {
                this.config.image.left -= 1;
            })
            .bind('ctrl+alt+shift+left', () => {
                this.config.image.left -= 10;
            })
            .bind('ctrl+alt+right', () => {
                this.config.image.left += 1;
            })
            .bind('ctrl+alt+shift+right', () => {
                this.config.image.left += 10;
            })
            .bind('ctrl+alt+up', () => {
                this.config.image.top -= 1;
            })
            .bind('ctrl+alt+shift+up', () => {
                this.config.image.top -= 10;
            })
            .bind('ctrl+alt+down', () => {
                this.config.image.top += 1;
            })
            .bind('ctrl+alt+shift+down', () => {
                this.config.image.top += 10;
            });
    },

    watch: {
        config: {
            handler(newVal) {
                db.setProps(newVal);
            },
            deep: true
        },
        image: {
            handler(newVal) {
                db.setImage(newVal);
            }
        }
    },

    methods: {
        handleImageChange(imageObject) {
            console.log(imageObject);
            this.image = imageObject;
        }
    },

    components: {
        Panel,
        Overlay
    }
}
</script>

<style>
</style>



